export default "/*\n* Font.\n*/\n/*\n* Body.\n*/\n/*\n* Screens.\n*/\n/*\n* Z-index.\n*/\n/*\n* Scrollbar.\n*/\n/*\n* Border Radius.\n*/\n/*\n* Spaces.\n*/\n/*\n* Controls (use for input || selector || buttons).\n*/\n/*\n* Animation.\n*/\n/*\n* Navbar.\n*/\n/*\n* Buttons.\n*/\n/*\n* Icons.\n*/\n/*\n* Search Input.\n*/\n/*\n* CheckBox.\n*/\n/*\n* Dropdown.\n*/\n/*\n* Card.\n*/\n/*\n* No Data.\n*/\n/*\n* Tabs.\n*/\n/**\n* Example of usage:\n* <div class=\"ab-checkbox\">\n*   <input type=\"checkbox\" aria-label=\"...\"/>\n* </div>\n*/\n.ab-checkbox {\n  position: relative;\n  cursor: pointer;\n}\n.ab-checkbox input {\n  position: relative;\n  border: none;\n  cursor: pointer;\n  -webkit-appearance: none;\n     -moz-appearance: none;\n          appearance: none;\n}\n.ab-checkbox input:after {\n  content: \"\";\n  display: block;\n  height: 24px;\n  width: 24px;\n  border: 2px solid var(--checkbox-border);\n  background-color: var(--checkbox-bg);\n  border-radius: 2px;\n}\n.ab-checkbox input:checked:after {\n  border-color: var(--checkbox-border-checked);\n}\n.ab-checkbox input:checked:before {\n  content: \"\";\n  transform: rotate(45deg);\n  position: absolute;\n  left: 8px;\n  top: 3px;\n  display: table;\n  width: 8px;\n  height: 14px;\n  border: 2px solid var(--checkbox-icon-checked);\n  border-top: 0;\n  border-left: 0;\n}\n.ab-checkbox input:focus {\n  outline: none;\n  box-shadow: 0 0 10px var(--focus-box);\n  z-index: 1;\n}\n/*\n* Font.\n*/\n/*\n* Body.\n*/\n/*\n* Screens.\n*/\n/*\n* Z-index.\n*/\n/*\n* Scrollbar.\n*/\n/*\n* Border Radius.\n*/\n/*\n* Spaces.\n*/\n/*\n* Controls (use for input || selector || buttons).\n*/\n/*\n* Animation.\n*/\n/*\n* Navbar.\n*/\n/*\n* Buttons.\n*/\n/*\n* Icons.\n*/\n/*\n* Search Input.\n*/\n/*\n* CheckBox.\n*/\n/*\n* Dropdown.\n*/\n/*\n* Card.\n*/\n/*\n* No Data.\n*/\n/*\n* Tabs.\n*/\n.ab-list__row {\n  display: flex;\n  padding: 16px;\n}\n.ab-list__row:nth-of-type(odd) {\n  background-color: var(--bg-zebra);\n}\n.ab-list__label {\n  padding-right: 8px;\n  font-weight: \"Proxima Bold\", Arial, sans-serif;\n}\n.ab-list__info {\n  padding: 16px;\n}\n:host {\n  flex: 1;\n  display: flex;\n  flex-direction: column;\n}\n.ab-color--primary {\n  color: var(--color-primary-dark);\n}\n/*\n* Card Grid.\n*/\n.ab-grid {\n  display: flex;\n  flex-wrap: wrap;\n  justify-content: center;\n  flex: 1;\n  overflow-x: auto;\n  margin: -8px;\n}\n.ab-grid__item {\n  width: 100%;\n  padding: 8px;\n}\n@media (min-width: 780px) {\n  .ab-grid__item {\n    width: calc(100% * 1 / 3 - 8px);\n    max-width: unset;\n  }\n}\n@media (min-width: 1200px) {\n  .ab-grid__item {\n    width: calc(100% * 1 / 4 - 8px);\n    max-width: unset;\n  }\n}\n/*\n* Toolbar for page & card.\n*/\n[class*=ab-toolbar] {\n  display: flex;\n  align-items: center;\n}\n[class*=ab-toolbar] .ab-checkbox {\n  margin-right: 16px;\n}\n.ab-toolbar--main {\n  padding: 16px 0;\n  border-top: 1px solid var(--color-gray);\n  background-color: var(--body-bg);\n  z-index: 1;\n}\n/*# sourceMappingURL=data:application/json;base64, */"